<template>
    <div>

        <loader :is-visible="isLoading"></loader>

        <section class="page-action">

            <div class="container-fluid p-0">
                <div v-if="LoadingMsg" style="position: absolute; top: 30%; z-index:9999999; left:0; right:0;">
                    <div class="alert alert-primary mx-auto w-30 text-center" role="alert">{{ $t("FileDownloadMsg") }}<em class="fa fa-download ml-3 font-18"></em></div>
                </div>
                <div class="row">

                    <div class="col-md-12 padding-t_8">

                        <div class="theme-primary partition p-actions-expand tablehead">
                            <span class="p-name text-white">{{ $t('UserManual') }}</span>
                            <router-link to="/Help/HelpAndGuide"><span class="p-actions"><a title=""
                                        class="p-action-btn text-white float-right" data-toggle-tooltip="tooltip"
                                        data-original-title="Back">
                                        <em class="fa fa-angle-double-left"></em><br />{{ $t("Back") }}</a>

                                </span> </router-link>
                            <span @click="DownloadZipFile(UrlList)" class="p-actions float-right"><a title=""
                                    class="p-action-btn text-white float-right" data-toggle-tooltip="tooltip"
                                    data-original-title="Download">
                                    <em class="fa fa-arrow-down"></em><br />{{ $t("Download") }}</a>

                            </span>

                        </div>
                    </div>
                </div>

            </div>

            <div class="row control_helpguide" style="margin-top:10px;">


                <div class="col-md-4 mb-3" v-for="item in UserManualList" :key="item">

                    <div class="border py-2 pr-3">
                        <div class="row d-flex align-items-center">
                            <div class="col-md-2 p-0">
                                <div class="text-center font-36 geraldine" v-html="item.icon">
                                </div>
                            </div>

                            <div class="col-md-8 p-0" id="Userlist">
                                <!-- <h5 class="card-title font-18">{{ item.ArticleCategoryName }}</h5> -->
                                <!-- <h6 class="card-title font-18">{{ item.Articlename }}</h6> -->
                                <p class="card-text font-14">{{ item.ModuleName }}</p>
                                <div class="description-hover" :title="GetHtmlContent(item.ArticleContent)">
                                    <span class="card-text font-12 description-text" 
                                        v-html="TruncateStr(item.ArticleContent, 100)"></span>
                                </div>

                            </div>
                            <div class="col-md-2">
                                <div class="text-right equator-color">
                                    <!-- <a :href="item.fileUrl" target="_blank">
                                        <em class="fa fa-eye font-weight-light text-info font-28 mr-2"
                                            aria-hidden="true"></em>
                                    </a> -->

                                    <a href="#"><em class="fa fa-download font-weight-light text-primary font-28"
                                            aria-hidden="true"
                                            @click="DownloadSingleFile(item.FileUrl, item.Articlename)"></em></a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


            </div>

            <div v-if="IsDataNotFoundShow">
                <div class="col-md-12">
                    <div class="border text-center text-danger no-record mt-4 p-2">
                        {{ $t('No_Record_Found') }}
                    </div>
                </div>
            </div>

        </section>
    </div>
</template>

<script>
import DataService from '../../services/DataService';

import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
export default
    {
        name: 'UserManual',
        data() {
            return {

                UserManualList: [],
                UrlList: [],
                isLoading: false,
                UserManualDescription: '',
                UserManualIcons: '',
                IsDataNotFoundShow: false,
                LoadingMsg: false

            }
        },
        created: async function () {
            await this.BindUserManualList();


        },
        methods: {
            TruncateStr(str, len) {
                var convertstring = '' + str;
                if (convertstring != null)
                    if (convertstring.length <= len) return str;
                    else return convertstring.substring(0, len) + "...";
            },
            BindUserManualList: async function () {

                var vm = this;
                vm.isLoading = true;

                var params = `search=&userType=${GetUserInfo.value.UserType}&articleType=UserManual&moduleId=`

                await DataService.GetArticleList(params).then(function (response) {

                    if (response.data != null && response.data.length > 0) {

                        response.data.forEach((value, index) => {

                            let myobj = {
                                Articlename: value.articleName,
                                ArticleCategoryName: value.articleCatagoryName,
                                ArticleId: value.articleId,
                                FileUrl: value.fileUrl,
                                ArticleContent: value.articlecontent.includes('\r\n') ? value.articlecontent.replaceAll('\r\n', '') : value.articlecontent,
                                //href: $(value.articleName).attr("href"),
                                //ManualDescription: vm.UserManualDescription,
                                icon: '<em class="fa ' + value.Icon + '" aria-hidden="true"></em>',
                                ModuleName: value.ModuleName
                            }
                            vm.UserManualList.push(myobj);

                        })


                        response.data.forEach((value, index) => {

                            if (value.fileUrl != null && value.fileUrl != "") {
                                vm.UrlList.push(value.fileUrl)
                            }

                        })
                        vm.isLoading = false;
                        vm.IsDataNotFoundShow = false;
                    }

                    else {
                        vm.isLoading = false;
                        vm.IsDataNotFoundShow = true;
                    }
                    vm.isLoading = false;
                });
            },
            GetHtmlContent:function(data){
                
                var vm=this;
                return data.split("<p>")[1].split("</p>")[0];
            },
            // downloadAll: function () {
            //       
            //     var cm = this;

            //     $('#Userlist').find('a').each(function () {

            //         cm.UrlList.push($(this).attr('href'));
            //     })


            //     for (var i = 0; i < this.UrlList.length; i++) {

            //         var link = document.createElement('a');

            //         link.setAttribute('href', this.UrlList[i]);

            //         //link.download = 'File';

            //         link.setAttribute('target', '_blank');

            //         link.style.display = 'none';

            //         document.body.appendChild(link);

            //         link.click();

            //     }

            //     document.body.removeChild(link);
            // },

            // downloadPdf: function (href) {

            //  

            //     var link = document.createElement('a');

            //     link.href = href;

            //     link.download = 'file.pdf';

            //     document.body.appendChild(link);

            //     link.click();
            // },
            // downloadAll: function () {
            //    
            //     this.UrlList = [];

            //     for (var i = 0; i < $('#Userlist').find('a').length; i++)
            //      {

            //         this.UrlList.push($('#Userlist').find('a').attr('href'));                    

            //     }

            //     for (var i = 0; i < this.UrlList.length; i++) 
            //     {
            //         var link = document.createElement('a');

            //         link.setAttribute('download', 'mohit');                    

            //         link.setAttribute('target', '_blank');

            //         link.style.display = 'none';

            //         link.setAttribute('href', this.UrlList[i]);

            //         document.body.appendChild(link);                    

            //         link.click();              




            //     }

            //     document.body.removeChild(link);
            // },
            DownloadSingleFile: function (fileUrl, fileName) {

                var vm = this;
                vm.isLoading = true;
                vm.LoadingMsg = true;

                // var FileUrl = 'https://cogniterblob.blob.core.windows.net/cg-rohit-enterprises-122628/thumb/63221/1164366/329a8ff0-fb3c-4a35-85e2-0f7cc9d6d317.jpg';

                if (fileUrl == null || fileUrl == "") {
                    vm.isLoading = false;
                    vm.ShowAlert(vm.$t("NoFileUrl"), "warning", true, vm.$t("Alert"));
                }
                else {

                    var params = `FileUrl=${fileUrl}&ModuleId=`

                    DataService.DownloadSingleFile(params).then((response) => {

                        if (response.data != null) {

                            var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                            var fileLink = document.createElement('a');
                            fileLink.href = fileURL;
                            fileLink.setAttribute('download', fileName + ".pdf");
                            document.body.appendChild(fileLink);
                            fileLink.click();
                            vm.isLoading = false;
                            vm.LoadingMsg = false;

                        }
                        else {
                            vm.isLoading = false;
                            vm.LoadingMsg = false;
                            vm.ShowAlert(vm.$t("FileNotDownload"), "warning", true, vm.$t("Alert"));
                        }

                    })

                }

            },

            DownloadZipFile: function (Url) {

                var vm = this;
                vm.isLoading = true;
                vm.LoadingMsg = true;


                if (Url == null || Url == "") {
                    vm.isLoading = false;
                    vm.ShowAlert(vm.$t("NoFileUrl"), "warning", true, vm.$t("Alert"));
                }
                else {

                    var ZipFileName = 'UserManual.zip';
                    // var params = `Url=${Url}`

                    var params = `Files=${Url}`
                    // var url = JSON.stringify(Url);
                    // var params = `FileUrl=${url}`

                    DataService.DownloadZipFile(params).then((response) => {


                        if (response.data != null) {
                            var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                            var fileLink = document.createElement('a');
                            fileLink.href = fileURL;

                            fileLink.setAttribute('download', ZipFileName);
                            document.body.appendChild(fileLink);
                            fileLink.click();
                            vm.isLoading = false;
                            vm.LoadingMsg = false;

                        }
                        else {
                            vm.isLoading = false;
                            vm.LoadingMsg = false;

                            vm.ShowAlert(vm.$t("FileNotDownload"), "warning", true, vm.$t("Alert"));
                        }

                    })

                }

            }



        }
    }


</script>

<style></style>

